import { AttackEffectType, Attack } from '../5eRules';

export interface Character {
	name: string;
	imageRef: string;
	size: CharacterSize;
	strength: number;
	dexterity: number;
	constitution: number;
	intelligence: number;
	wisdom: number;
	charisma: number;
	speed: CharacterSpeeds;
}

export interface PlayerCharacter extends Character {
	id: string;
	ac: number;
	hp: number;
	maxHp: number;
	hitDice: number;
	levels: CharacterLevel[];
	equipment: CharacterEquipment[];
	attacks: CharacterAttack[];
	spells: CharacterSpell[];
	saves: CharacterProficiencySaves;
	skills: CharacterProficiencySkills;
	traits: CharacterTrait[];
	resources: CharacterResource[];
	spellSlots: CharacterSpellSlots;
	money: CharacterMoney;
}

export interface NonPlayerCharacterIndex {
	id: string;
	name: string;
	cr: string;
	environments: string[];
}

export interface NonPlayerCharacter extends Character {
	class: string;
	alignment: string;
	environments: string[];
	source: string;
	cr: number;
	ac: number;
	acType: string;
	hpDice: string;
	actions: Attack[];
	reactions: Attack[];
	legendaryActions: Attack[];
	legendaryActionCount?: number;
	languages: string;
	saves: NonPlayerCharacterSaves;
	damageResistances: string;
	damageImmunities: string;
	conditionImmunities: string;
	traits: NonPlayerCharacterTrait[];
	senses: CharacterSenses;
	skills: NonPlayerCharacterSkill[];
	variants?: string[];
}

export enum CharacterSize {
	Tiny = 0,
	Small = 1,
	Medium = 2,
	Large = 3,
	Huge = 4,
	Gargantuan = 5
}
export interface CharacterSpeeds {
	walk: number;
	burrow?: number;
	climb?: number;
	fly?: number;
	swim?: number;
}

export interface CharacterProficiencySaves {
	strength?: number;
	dexterity?: number;
	constitution?: number;
	intelligence?: number;
	wisdom?: number;
	charisma?: number;
}

export interface CharacterProficiencySkills {
	acrobatics?: number;
	animalHandling?: number;
	arcana?: number;
	athletics?: number;
	deception?: number;
	history?: number;
	insight?: number;
	intimidation?: number;
	investigation?: number;
	medicine?: number;
	nature?: number;
	perception?: number;
	performance?: number;
	persuasion?: number;
	religion?: number;
	sleightOfHand?: number;
	stealth?: number;
	survival?: number;
}

export interface CharacterSpellSlots {
	1?: CharacterSpellSlot;
	2?: CharacterSpellSlot;
	3?: CharacterSpellSlot;
	4?: CharacterSpellSlot;
	5?: CharacterSpellSlot;
	6?: CharacterSpellSlot;
	7?: CharacterSpellSlot;
	8?: CharacterSpellSlot;
	9?: CharacterSpellSlot;
}
export interface CharacterSpellSlot {
	current: number;
	max: number;
}
export interface CharacterResource {
	name: string;
	quantity: number;
	max: number;
}
export interface CharacterTrait {
	name: string;
	description: string;
}

export interface CharacterLevel {
	className: string;
	level: number;
}

export interface CharacterMoney {
	cp: number;
	sp: number;
	ep: number;
	gp: number;
	pp: number;
}

export interface CharacterEquipment {
	name: string;
	type: string;
	weight?: number;
	quantity?: number;
	attacks?: CharacterAttack[];
	notes?: string;
}

export interface CharacterAttack {
	title: string;
	range: number;
	longRange?: number;
	effects: CharacterAttackEffect[];
}

export interface CharacterAttackEffect {
	type: AttackEffectType;
}

export interface ToHitCharacterAttackEffect extends CharacterAttackEffect {
	ability: string;
}

export interface DamageCharacterAttackEffect extends CharacterAttackEffect {
	ability?: string;
	diceCount: number;
	diceSize: number;
	damageType: string;
	bonus?: number;
}

export interface SavingThrowCharacterAttackEffect extends CharacterAttackEffect {
	saveType: string;
	DCAbility: string;
	onSave: CharacterAttackEffect;
	onFail: CharacterAttackEffect;
}

export interface TextCharacterAttackEffect extends CharacterAttackEffect {
	text: string;
}

export interface CharacterSpell {
	name: string;
	level: number;
	school: string;
	time: string;
	range: number;
	verbal?: boolean;
	somatic?: boolean;
	material?: string;
	duration: string;
	description: string;
	higherLevels?: string;
	effects: CharacterAttackEffect[];
}

export interface NonPlayerCharacterTrait {
	title: string;
	description: string;
}

export interface CharacterSenses {
	tremorsense?: number;
	blind?: boolean;
	blindsight?: number;
	truesight?: number;
	darkvision?: number;
}

export interface NonPlayerCharacterSkill {
	skill: string;
	modifier: number;
}

export interface NonPlayerCharacterSaves {
	strength?: number;
	dexterity?: number;
	constitution?: number;
	intelligence?: number;
	wisdom?: number;
	charisma?: number;
}
